body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Parkinsans", sans-serif;
}

.loading-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: white;
}
.loading-container .loading-content {
  text-align: center;
}
.loading-container .loading-logo {
  width: 5vw;
}
.loading-container .loading-spinner {
  border: 0.2vw solid rgba(255, 255, 255, 0.3);
  border-top: 0.2vw solid red;
  border-radius: 50%;
  width: 2.7vh;
  height: 2.7vh;
  animation: spin 0.7s linear infinite;
  margin-top: 4.2vh;
  margin-left: 1.8vw;
}
@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.logincontainer {
  margin: 1vh 10vw;
  margin-top: 20vh;
}
.logincontainer .logindetails {
  display: flex;
  flex-direction: column;
}
.logincontainer .loginlogo img {
  width: 17vw;
}
.logincontainer .login {
  margin: 7vh 3vw;
}
.logincontainer .login a {
  text-decoration: none;
}
.logincontainer .loginbutton {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 0.2vw solid rgb(225, 224, 224);
  border-radius: 0.7vw;
  width: 35%;
  margin-top: 4vh;
  padding: 2vh 2vw;
}
.logincontainer .loginbutton:hover {
  background-color: rgb(233, 233, 233);
  cursor: pointer;
}
.logincontainer .loginbutton img {
  height: 3vh;
  margin-bottom: 0.5vh;
  margin-right: 0.5vw;
}
.logincontainer .loginbutton button {
  font-size: 1.4vw;
  font-weight: 700;
  border: none;
  background-color: transparent;
  cursor: pointer;
}
.logincontainer .terms-policy {
  margin: 0 3.3vw;
  position: relative;
  bottom: 5vh;
}
.logincontainer .terms-policy p {
  width: 36%;
  font-size: 1vw;
}
.logincontainer .terms-policy p a {
  color: black;
}
.logincontainer .loginimage {
  position: absolute;
  top: 30vh;
  right: 8vw;
}
.logincontainer .loginimage img {
  width: 40vw;
}
.logincontainer .loading-spinner {
  border: 0.3vw solid rgba(255, 255, 255, 0.3);
  border-top: 0.3vw solid red;
  border-radius: 50%;
  width: 5vh;
  height: 5vh;
  animation: spin 0.5s linear infinite;
  margin-top: 2vh;
  margin-left: 4vw;
}
@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.Loggedin {
  text-align: center;
  margin-top: 20px;
}

.dashboard-container {
  display: flex;
  flex-direction: column;
}
.dashboard-container h1 {
  font-size: 2vw;
  font-weight: 500;
}
.dashboard-container .logout-button {
  height: 5vh;
  width: 4vw;
}/*# sourceMappingURL=main.css.map */